function string_of_tag(param) {
  switch (param) {
    case /* Advanced */0 :
        return "advanced";
    case /* Beginner */1 :
        return "beginner";
    case /* Interested */2 :
        return "interested";
    case /* Intermediary */3 :
        return "intermediary";
    case /* NotInterested */4 :
        return "not-interested";
    case /* Toybox */5 :
        return "toybox";
    case /* WorkExperience */6 :
        return "work-experience";
    
  }
}

function color_of_tag(param) {
  switch (param) {
    case /* Advanced */0 :
        return Css.rgb(255, 129, 142);
    case /* Beginner */1 :
        return Css.rgb(249, 255, 163);
    case /* Interested */2 :
        return Css.rgb(145, 215, 255);
    case /* Intermediary */3 :
        return Css.rgb(255, 159, 91);
    case /* NotInterested */4 :
        return Css.rgb(222, 222, 222);
    case /* Toybox */5 :
        return Css.rgb(245, 159, 255);
    case /* WorkExperience */6 :
        return Css.rgb(188, 255, 193);
    
  }
}

